function OrderFilling(props) {
  const { optionReducer, seatsRducer } = props;
  const [timeLineData, setTimeLineData] = useState({});
  const [isshow, setIsshow] = useState(false);
  const [peoplesArr, setPeoplesArr] = useState([]);
  const [childsArr, setChildsArr] = useState([]);
  const dispatch = useDispatch();
  const store = useSelector((state) => {
    return state;
  });

  useEffect(() => {
    const url = new URI();
    const urlData = url.search(true);
    setTimeLineData(urlData);
  }, []);
  const addPerson = () => {
    let arr = peoplesArr;
    arr.push({
      id: Date.now(),
    });
    setPeoplesArr([...arr]);
  };

  const addChild = () => {
    let arr = childsArr;
    arr.push({
      id: Date.now(),
    });
    setChildsArr([...arr]);
    console.log("childsArr", childsArr);
  };

  const handleDelete = (e) => {
    const arr = peoplesArr.filter((people) => {
      return people.id !== e.id;
    });
    setPeoplesArr([...arr]);
  };

  const showSeleted = (e) => {
    console.log("e", e);
    switch (e) {
      case "ticket":
        const arr = [
          {
            label: "",
            value: 1,
          },
          {
            label: "",
            value: 2,
          },
          {
            label: "",
            value: 3,
          },
        ];
        dispatch(createdTickets(arr));
        dispatch(createdSeats(seatsRducer));
        setIsshow(true);
        return;
      case "person":
        const arr1 = [
          {
            label: "",
            value: "1",
          },
          {
            label: "",
            value: "2",
          },
        ];
        dispatch(createdPerson(arr1));
        setIsshow(true);
        return;
      case "child":
        dispatch(createdChild());
        dispatch(createdChild());
        return;
      default:
    }
  };
  const handleshow = (e) => {
    setIsshow(e);
  };

  return (
    <div>
      <Header title={""} isBack={true} />
      <TimeLIneModule>
        {/* <p> {store}</p> */}
        <p>
          
          {timeLineData.c}
        </p>
      </TimeLIneModule>
      <div style={{ display: "flex", justifyContent: "space-around" }}>
        <div>
          <label style={{ marginRight: "50px" }}></label>
          <span
            onClick={() => {
              showSeleted("ticket");
            }}
          >
            {seatsRducer}
          </span>
        </div>
        <label style={{ color: "skyblue" }}>748</label>
      </div>
      {peoplesArr.map((people, idx) => {
        return (
          <Person
            key={idx}
            handleDelete={handleDelete}
            people={people}
            onselected={showSeleted}
          ></Person>
        );
      })}

      {childsArr.map((childs, idx) => {
        return <Child key={idx} onselected={showSeleted} />;
      })}
      <div className={style["add-person"]}>
        <p
          onClick={addPerson}
          style={{
            width: "50%",
            lineHeight: "50px",
            textAlign: "center",
            backgroundColor: "skyblue",
          }}
        >
          
        </p>
        <p
          onClick={addChild}
          style={{
            width: "50%",
            lineHeight: "50px",
            textAlign: "center",
            backgroundColor: "skyblue",
          }}
        >
          
        </p>
      </div>
      {isshow && (
        <UseSelected
          options={[...optionReducer]}
          isshow={isshow}
          onselected
          onShow={handleshow}
        />
      )}
    </div>
  );
}
export default connect(
  function mapStateToProps(state) {
    // console.log('state', state)
    return state;
  },
  function mapDispatchToProps(dispatch) {
    return { dispatch };
  }
)(OrderFilling);
